.analogClock {
  display: flex;
  text-align: center;
  font-size: 2rem;
  min-height: 1vh;
  align-items: center;
}

.clock {
  width: 10rem;
  height: 10rem;
  border: 0.5rem solid #536170;
  border-radius: 50%;
  margin: auto;
  position: relative;
  padding: 2rem;
}

.clock-faces {
  position: relative;
  width: 100%;
  height: 100%;
  transform: translateY(-3px);
}

.hand {
  width: 50%;
  height: 6px;
  background: black;
  position: absolute;
  top: 50%;
  transform-origin: 100%;
  transform: rotate(90deg);
  transition: all 0.05s;
  transition-timing-function: ease-in-out;
}

.hour-hand {
  width: 40%;
  height: 0.5rem;
  margin-left: 1rem;
}

.sec-hand {
  height: 3px;
  background: #ff4646;
}
